h1,
h2,
h3,
h4,
h5,
h6,
p {
  margin: 0;
}

img {
  display: block;
  max-width: 100%;
  height: auto;
}

ul {
  padding: 0;
  margin: 0;
  list-style: none;
}

a {
  text-decoration: none;
  cursor: url('../images/cursor-hand.svg'), pointer;
}

*,
:before,
:after {
  box-sizing: border-box;
}

:root {
  --bg-color: #f4f4f4;
  --text-color: #111321;
  --color-btn-favorite: #111321;
  --white-color: #fff;
  --accent-color: #4440f6;
  --gray-color: #a8a8a8;
  --footer-color: #7a7a7a;
  --switcher-gray-moon: #5f6775;
  --switcher-accent-day: #4b48db;
}

:root body.darkMode {
  --bg-color: #2e2e2e;
  --text-color: #f4f4f4;
  --switcher-accent-day: #f4f4f4;
  --switcher-gray-moon: #4440f7;
}

html,
body {
  height: 100%;
  scroll-behavior: smooth;
}

body {
  height: 100vh;
  background-color: var(--bg-color);
  color: var(--text-color);
  font-family: Manrope;
  margin: 0;
}

button {
  cursor: url('../images/cursor-hand.svg'), pointer;
  background-color: inherit;
  box-shadow: none;
  border: none;
  margin: 0;
  padding: 0;
  font-family: inherit;
}

svg {
  fill: currentColor;
}

.wraper {
  min-height: 100%;
  flex-direction: column;
  display: flex;
}

.main {
  flex: auto;
}

.container {
  max-width: 480px;
  margin: 0 auto;
  padding-left: 16px;
  padding-right: 16px;
}

@media screen and (min-width: 767.98px) {
  .container {
    max-width: 768px;
  }
}

@media screen and (min-width: 1279.98px) {
  .container {
    max-width: 1280px;
  }
}

.is-hidden {
  width: 1px;
  height: 1px;
  clip: rect(0 0 0 0);
  border: 0;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

body.is-fixed {
  width: 100%;
  height: 100vh;
  overflow: hidden;
}

.opacity {
  opacity: 0.5;
}

.no-opacity {
  opacity: 0.9;
}

